import AbstractLanguage from '../classes/abstract-language.js'

/**
 * Japanese language converter.
 *
 * Converts numbers to Japanese kanji numerals using the Sino-Japanese system:
 * - Uses kanji characters (, , , etc.)
 * - Grouping by  (man, 10,000) and  (oku, 100,000,000)
 * - Unique scale units:  (ch, trillion),  (kei, 10^16)
 * - Special rules for 1: omitted before  (10),  (100),  (1000), but kept for  and above
 *
 * Key Features:
 * - Sino-Japanese number system (standard for general counting)
 * - Grouping by powers of 10,000 (-based system, not 1,000)
 * - Scale units:  (10^4),  (10^8),  (10^12),  (10^16)
 * - Special handling of  (one) prefix
 * - Support for very large numbers up to  (10^68)
 *
 * Algorithm:
 * 1. Split number into groups of 4 digits (-based, not 3-digit thousand-based)
 * 2. Convert each group to kanji using special rules
 * 3. Append appropriate scale word (, , , etc.)
 * 4. Join all groups
 *
 * Special Rules:
 * - 10, 100, 1000: Don't use  prefix (, ,  not , , )
 * - 10,000+: Keep  prefix (, , )
 * - Zero:  or  (both acceptable,  is more formal)
 */
export class Japanese extends AbstractLanguage {
  negativeWord = ''
  decimalSeparatorWord = ''
  zeroWord = ''
  wordSeparator = '' // Japanese doesn't use spaces between characters
  convertDecimalsPerDigit = true // Enable digit-by-digit decimal conversion

  // Digits used for group conversion (1-9)
  digits = ['', '', '', '', '', '', '', '', '']

  // Scale words for grouping by 10^4
  scales = [
    '', // 10^0 (ones)
    '', // 10^4 (man)
    '', // 10^8 (oku)
    '', // 10^12 (ch)
    '', // 10^16 (kei)
    '', // 10^20 (gai)
    '', // 10^24 (jo/shi)
    '', // 10^28 (j)
    '', // 10^32 (k)
    '', // 10^36 (kan)
    '', // 10^40 (sei)
    '', // 10^44 (sai)
    '', // 10^48 (goku)
    '', // 10^52 (ggasha)
    '', // 10^56 (asgi)
    '', // 10^60 (nayuta)
    '', // 10^64 (fukashigi)
    '' // 10^68 (murytais)
  ]

  /**
   * Converts a group of up to 4 digits to Japanese kanji.
   * Handles special rules for omitting  before , , .
   *
   * Rule: Within a group, omit  before // EXCEPT when:
   * - It's a lower group (not isTopGroup) AND
   * - It would be the only character in that position
   *
   * @param {bigint} num - Number from 0 to 9999
   * @param {boolean} isTopGroup - Whether this is the highest non-zero group
   * @returns {string} Japanese kanji representation
   */
  convertGroup (num, isTopGroup = false) {
    if (num === 0n) return ''

    const thousands = num / 1000n
    const hundreds = (num % 1000n) / 100n
    const tens = (num % 100n) / 10n
    const ones = num % 10n

    let result = ''

    // Thousands ()
    if (thousands > 0n) {
      // Always omit  before  when thousands === 1
      if (thousands === 1n) {
        result += ''
      } else {
        result += this.digits[Number(thousands) - 1] + ''
      }
    }

    // Hundreds ()
    if (hundreds > 0n) {
      // Always omit  before  when hundreds === 1
      if (hundreds === 1n) {
        result += ''
      } else {
        result += this.digits[Number(hundreds) - 1] + ''
      }
    }

    // Tens ()
    if (tens > 0n) {
      // Always omit  before  when tens === 1
      if (tens === 1n) {
        result += ''
      } else {
        result += this.digits[Number(tens) - 1] + ''
      }
    }

    // Ones
    if (ones > 0n) {
      result += this.digits[Number(ones) - 1]
    }

    return result
  }

  /**
   * Converts a BigInt number to Japanese cardinal form.
   *
   * @param {bigint} number - The number to convert
   * @returns {string} Japanese kanji representation
   */
  convertWholePart (number) {
    if (number === 0n) {
      return this.zeroWord
    }

    let temp = number
    let scaleIndex = 0
    const groups = []

    // Split into groups of 4 digits (-based system)
    while (temp > 0n) {
      const group = temp % 10000n
      if (group > 0n) {
        groups.push({ value: group, scale: scaleIndex })
      }
      temp = temp / 10000n
      scaleIndex++
    }

    // Reverse to process from highest to lowest
    groups.reverse()

    let result = ''

    for (let i = 0; i < groups.length; i++) {
      const { value, scale } = groups[i]
      const isTopGroup = (i === 0)

      const groupStr = this.convertGroup(value, isTopGroup)

      // For scales >= 1 ( and above), always add the scale word
      if (scale >= 1) {
        // Special case: if group is 1 and scale >= 1, we need  before the scale
        if (value === 1n) {
          result += '' + this.scales[scale]
        } else {
          result += groupStr + this.scales[scale]
        }
      } else {
        result += groupStr
      }
    }

    return result
  }
}

/**
 * Converts a value to cardinal (written) form in Japanese.
 *
 * @param {number|string|bigint} value - Number to convert.
 * @param {Object} [options] - Options for the converter.
 * @returns {string} Value in Japanese kanji numerals.
 * @throws {Error} If value is invalid.
 *
 * @example
 * convertToWords(42, { lang: 'ja' }); // ''
 * convertToWords(1000, { lang: 'ja' }); // ''
 * convertToWords(10000, { lang: 'ja' }); // ''
 */
export default function convertToWords (value, options = {}) {
  return new Japanese(options).convertToWords(value)
}
